import { ReactNode } from 'react';
import { toast, cssTransition, Slide, ToastOptions } from 'react-toastify';

export const ReactToast = toast;

const None = cssTransition({
  enter: 'zoomIn',
  exit: 'zoomOut',
  duration: 10,
});

export type ToastPosition =
  | 'bottom-right'
  | 'bottom-left'
  | 'bottom-center'
  | 'top-right'
  | 'top-left'
  | 'top-center';

export interface ToastProps {
  /** Toast content */
  content: ReactNode;
  /** Toast position */
  position?: ToastPosition;
  /** containerId */
  containerId?: string | number;
  /** toastId */
  toastId?: string | number;
  animate?: boolean;
  autoClose?: number | false;
  closeOnClick?: boolean;
  additionalToastOps?: ToastOptions;
}

const Toast = (props: ToastProps): void => {
  const {
    content,
    position = 'bottom-right',
    containerId,
    animate = true,
    autoClose,
    closeOnClick,
    toastId,
    additionalToastOps,
  } = props;

  ReactToast(content, {
    position,
    containerId,
    closeOnClick: closeOnClick,
    transition: animate ? Slide : None,
    autoClose: autoClose,
    toastId: toastId,
    ...additionalToastOps,
  });
};

export default Toast;
